import axios from 'axios';

const url = 'api/planets/'

class PlanetService {
    //GET ALL
    static getPlanets() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(url).then((res) => {
                    const data = res.data;
                    resolve(
                        data.map(planet => ({
                            ...planet,
                            createdAt: new Date(planet.createdAt)
                        }))
                    )
                })
            } catch(err) {
                reject(err);
            }
        });
    }

    //CREATE ONE
    static insertPlanet(name, question, answer, bool) {
        if(bool === "true") { 
            bool = 1
        } else { 
            bool = 0
        }
        return axios.post(url, {
            name,
            question,
            answer,
            bool,
            emotion: Math.floor(Math.random() * 20)
        });
    }

    //DELETE ONE
    static deletePlanet(id) {
        return axios.delete(`${url}${id}`);
    }

    //FIND ONE
    // static findPlanet() {
        
    // }

}

export default PlanetService;