<template>
  <div>
    <b-container>
      
      <b-col cols="6">
        <h1>Planets</h1>
        <!--CREATE PLANET HERE-->
        <div class="create-planet">
          <label for="create-planet">Make a Planet</label>
          <br>
          <input type="text" id="create-planet" v-model="name" placeholder="Planet Name">
          <input type="text" id="planet-question" v-model="question" placeholder="What is its string?"><br>
          <input type="radio" id="booltrue" v-model="bool" value="true">
          <label for="booltrue" >True</label>
          <input type="radio" id="boolfalse" v-model="bool" value="false">
          <label for="boolfalse">False</label><br>
          <!--<input type="text" id="planet-answer" v-model="answer" placeholder="What is its answer?">-->
          <br>
          <button v-on:click="createPlanet">New Planet!</button>

        </div>
      </b-col>
      <b-col cols="4">{{ mainPlanet.name }}</b-col>
    </b-container>
    <b-container>
      <b-col cols="6" class="iline">
        <hr>
        <p class="error" v-if="error">{{ error }}</p>
        <div class="planets-container">
          <div class="planet"
            v-for="(planet, index) in planets"
            v-bind:item = "planet"
            v-bind:index = "index"
            v-bind:key = "planet._id" 
            v-on:click = "showId(planet._id)"
            v-on:dblclick = "deletePlanet(planet._id)"
          >
            <div  v-bind:class="{green: planet.bool}" v-if="planet.bool == 1">
                <!--<img alt="emotion image" :src="{{ planet.imgUrl }}">-->
                <p class="name">{{ planet.name }} -- String is: {{ planet.question }}</p>
                <p class="info">Number is: {{ planet.emotion }}</p>
                <p v-if="planet.bool == 1">true</p>
                <p v-if="planet.bool == 0">false</p>
                <p>{{ totalTrue += planet.emotion }}</p>
                <!--<img alt="emotion image" v-bind:src = " `/images/${planet.imgUrl}` ">-->
            <hr>
            </div>
          </div>
        <p>Total is: {{ totalTrue }}</p>
        </div>
      </b-col>
      <b-col cols="6" class="iline">
        <hr>
        <p class="error" v-if="error">{{ error }}</p>
        <div class="planets-container">
          <div class="planet"
            v-for="(planet, index) in planets"
            v-bind:item = "planet"
            v-bind:index = "index"
            v-bind:key = "planet._id" 
            v-on:click = "showId(planet._id)"
            v-on:dblclick = "deletePlanet(planet._id)"
          >
            <div v-bind:class="{red: !(planet.bool)}" v-if="planet.bool == 0">
                <!--<img alt="emotion image" :src="{{ planet.imgUrl }}">-->
                <p class="name">{{ planet.name }} -- String is: {{ planet.question }}</p>
                <p class="info">Number is: {{ planet.emotion }}</p>
                <p v-if="planet.bool == 1">true</p>
                <p v-if="planet.bool == 0">false</p>
                <p>{{ totalFalse += planet.emotion }}</p>
                <!--<img alt="emotion image" v-bind:src = " `/images/${planet.imgUrl}` ">-->
            <hr>
            </div>
          </div>
        <p>{{ totalFalse }}</p>
        </div>

      </b-col>
    </b-container>
  </div>
</template>

<script>
import PlanetService from '../PlanetService';

export default {
  name: 'PlanetComponent',
  data() {
    return {
      planets: [],
      error: '',
      name: '',
      question: '',
      answer: '',
      bool: '',
      mainPlanet: {
        name: '',
        question: ''
      },
      totalTrue: 0,
      totalFalse: 0
    }
  },
  async created() {
    try {
      this.planets = await PlanetService.getPlanets();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPlanet() {
      await PlanetService.insertPlanet(this.name, this.question, this.answer, this.bool);
      this.planets = await PlanetService.getPlanets();
    },
    async deletePlanet(id) {
      await PlanetService.deletePlanet(id);
      this.planets = await PlanetService.getPlanets();
    },
    async showId(id) {
      this.mainPlanet.name = 'herehere' + id;
    }
  }
}
</script>

